:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {

}

.output {

}

.bar {
	border: 1px solid black;
	height: 24px;
}

.progress {
	height: 100%;
	background: #34495e;
	background: var(--selected-background);
}



